/* trying to get particle integration here */
.App {
  width: 100vw;
  /* make sure no background color set here */
}

.app-container {
  /* margin top because sticky nav bar */
  margin-top: 75px;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ParticlesBackground {
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*====== HOMEPAGE STYLING ======*/
.HomePage {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-container {
  margin-top: 10px;

  display: flex;
  position: relative;
  max-width: 750px;

  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #e3f2fd;
  border-radius: 30px;
  border: 1px solid silver;
  box-shadow: 7px 7px rgb(228, 221, 221);
}

.portrait > img {
  width: 310px;
  height: 450px;
  object-fit: cover;
  border-radius: 30px;
  border: 8px solid silver;
  margin: 10px;
}

.info {
  margin: 10px;
  width: 330px;
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title > h2,
h3,
p {
  text-align: center;
  color: black;
}

.social-icons-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}

.social-icon-btn {
  margin: 0 5px;
}

.social-icon-btn:hover {
  cursor: pointer;
}

.techstack-info {
  margin-top: 20px;
  min-width: 320px;

  flex-direction: column;
  align-items: center;
  text-align: center;
}

.note {
  position: relative;
  min-width: 320px;
  max-width: 750px;
}

/*====== MODAL STYLING ======*/
.modal-body-div {
  text-align: left;
}

/*====== PROJECTS PAGE STYLING ======*/
.works-container {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.works-container > h1 {
  position: relative;
  color: black;
}

.cards-container {
  min-width: 320px;
  max-width: 750px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

/*====== CONTACT PAGE STYLING ======*/

.ContactPage {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-div {
  position: relative;
  margin: 20px;
  min-width: 320px;
  max-width: 750px;
  width: 100%;
  border: 1px silver solid;
  border-radius: 20px;
  background-color: #e3f2fd;
  padding: 30px;
  box-shadow: 7px 7px rgb(228, 221, 221);
}

/*====== CONFIRMATION PAGE STYLING ======*/
.ConfirmationPage {
  position: relative;
  margin: 20px;
  min-width: 320px;
  max-width: 750px;
  text-align: center;
  border: 1px solid silver;
  padding: 30px;
  background-color: #e3f2fd;
  border-radius: 20px;
  box-shadow: 7px 7px rgb(228, 221, 221);
}
